.the-wizard {
  padding: 60px 0 120px;
  background: #031472;
  position: relative;
  overflow: hidden;
}
.the-wizard::after {
    content: '';
    position: absolute;
    left: 50%;
    transform: translate(-50%, 0);
    z-index: 0;
    top: -100px;
    width: 100%;
    max-width: 1600px;
    height: 180px;
    background: #031576;
    border-radius: 50%;
    filter: blur(20px);
}
.the-wizard__wrapper {
    width: 100%;
    max-width: 1310px;
    padding: 0 30px;
    box-sizing: border-box;
    margin: 0 auto;
    position: relative;
    z-index: 1;
}
@media only screen and (max-width: 640px) {
.the-wizard__wrapper {
        padding: 0 15px;
}
}
.the-wizard__header {
    font-size: 36px;
    font-weight: 500;
    letter-spacing: -.5px;
    color: #fff;
    padding-bottom: 38px;
    text-align: center;
}
.the-wizard__highlight {
    color: #00E597;
}
.the-wizard__steps {
    display: flex;
    border-bottom: 2px solid rgba(255, 255, 255, 0.07);
    margin-bottom: 40px;
}
.the-wizard__step-item {
    flex: 1 1 0;
    padding-bottom: 35px;
    position: relative;
    text-align: center;
}
.the-wizard__step-item.is-active:after {
      content: '';
      display: block;
      width: 100%;
      height: 2px;
      position: absolute;
      left: 0;
      bottom: -2px;
      background: linear-gradient(to right, rgba(0, 165, 108, 0) 0%, #03ffb6 50%, rgba(0, 165, 108, 0) 100%);
}
.the-wizard__step-item.is-active:before {
      content: '';
      display: block;
      position: absolute;
      z-index: 1;
      bottom: -9px;
      left: calc(50% - 9px);
      width: 15px;
      height: 15px;
      background: #03146F;
      border: 5px solid #03EDAD;
      border-radius: 50%;
}
.the-wizard__step-item.is-active .the-wizard__step-item-label,
    .the-wizard__step-item.is-active .the-wizard__step-item-name {
      color: #29E7A7;
      opacity: 1;
}
.the-wizard__step-item.is-prev .the-wizard__step-check {
      position: absolute;
      left: calc(50% - 9px + 3px);
      bottom: -6px;
      right: 0;
      width: 18px;
      height: 15px;
      z-index: 2;
}
.the-wizard__step-item.is-prev:after {
      content: '';
      display: block;
      width: 100%;
      height: 2px;
      position: absolute;
      left: 0;
      bottom: -2px;
      background: linear-gradient(to right, rgba(0, 165, 108, 0) 0%, rgba(3, 255, 182, 0.5) 50%, rgba(0, 165, 108, 0) 100%);
      transition: 0.3s;
}
.the-wizard__step-item.is-prev:before {
      content: '';
      display: block;
      position: absolute;
      z-index: 1;
      bottom: -9px;
      left: calc(50% - 9px);
      width: 18px;
      height: 18px;
      background: #167C88;
      border-radius: 50%;
      transition: 0.3s;
}
.the-wizard__step-item.is-prev .the-wizard__step-item-label {
      color: rgba(0, 233, 151, 0.5);
      opacity: 1;
      transition: 0.3s;
}
.the-wizard__step-item.is-prev .the-wizard__step-item-name {
      color: rgba(41, 231, 167, 0.5);
      opacity: 1;
      transition: 0.3s;
}
.the-wizard__step-item-label {
    text-transform: uppercase;
    font-size: 15px;
    letter-spacing: -.25px;
    font-weight: 500;
    color: #81A8F7;
    opacity: .5;
    padding-bottom: 2px;
    transition: color .15s ease-in-out, opacity .15s ease-in-out;
}
.the-wizard__step-item-name {
    font-size: 19px;
    line-height: 30px;
    font-weight: 500;
    color: #fff;
}
@media only screen and (max-width: 900px) {
.the-wizard__step-item-name {
        font-size: 16px;
        line-height: 18px;
}
}

