      height: 100%;
      box-shadow: 0 5px 9px rgba(31, 46, 233, 0.08);
      padding: 70px 30px 30px 60px;
      width: 330px;
      transition: 0.3s;
      transform: translateX(270px);
}
.page-faq__main-sidebar:hover {
        transform: translateX(0);
}
}
@media only screen and (max-width: 640px) {
.page-faq__main-sidebar {
      padding-top: 70px;
}
}
@media only screen and (max-width: 420px) {
.page-faq__main-sidebar {
      position: static;
      transform: translateX(0);
      box-shadow: none;
      width: 100%;
      padding: 0;
      background: transparent;
      height: auto;
}
}
